import React from 'react';

function Note(props) {
  return (
  
      <div className="Note ">
    <h2>{props.title}</h2>

    
    <p>{props.content}</p>
        <button onClick={props.delete} className="" >
       <i className='fas fa-trash-alt'></i>
        </button>
 
   </div>
  );
}

export default Note;
