import React, { useState } from 'react';
// import AddIcon from '@material-ui/icons/AddIcon';



function CreateNote(props) {

const  [expand, setExpand] = useState(false);

const [note, setNote] = useState({
  title: '',
  content: ' '

});

const InputEvent = (event) =>{
 
   const {name, value} = event.target

  setNote( (PrevData)=>{
    return {
        ...PrevData, 
        [name]: value,
    }
  })
  console.log(note)
}

const AddEvent = ()=>{
    props.passNote(note);
    setNote( {
      title: "",
      content: ""
    })
}
const expandIt = () => {
   setExpand(true)
}
  return (
    <div className='container'>
    <div className="CreateNote form-group">
      
        <input type='text' value={note.title} onChange={InputEvent} placeholder='Title' name='title'   onClick={expandIt} /> 

        { expand ? 
        <textarea rows='3' placeholder='Write A Note...'  value={note.content} required onChange={InputEvent}  
         name='content' >
        </textarea>: null }
       <button onClick={AddEvent} className="btn">
        <i className='fas fa-plus'></i>
       </button>
      </div>
    </div>
  );
}

export default CreateNote;
